.checkbox-old {
  display: none; }

.checkbox-custom {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  width: 20px;
  height: 20px;
  border-radius: 0px;
  background: rgba(255, 255, 255, 0.06);
  border: 1px solid rgba(255, 255, 255, 0.6);
  transition: 0.3s;
  cursor: pointer; }

.checkbox-label {
  display: inline-block;
  vertical-align: middle;
  color: rgba(255, 255, 255, 0.3);
  padding-left: 8px;
  transition: 0.3s;
  font-size: 14px;
  line-height: 20px;
  cursor: pointer; }

.checkbox-old:checked + .checkbox-custom {
  background: white;
  transition: 0.3s; }

.checkbox-old:checked + .checkbox-custom::before {
  content: "";
  display: block;
  position: absolute;
  left: 4px;
  top: 5px;
  width: 10px;
  height: 5px;
  border-bottom: 2px solid black;
  border-left: 2px solid black;
  transform: rotate(-50deg) skew(-5deg);
  border-radius: 2px;
  transition: 0.3s; }

.checkbox-old:checked + .checkbox-custom + .checkbox-label {
  color: rgba(255, 255, 255, 0.8); }

.checkbox-old[disabled] + .checkbox-custom {
  background: rgba(255, 255, 255, 0.012) !important;
  border: 1px solid rgba(255, 255, 255, 0.12) !important;
  transition: 0.3s;
  cursor: default; }

.checkbox-old[disabled] + .checkbox-custom::before {
  display: none !important; }

.checkbox-old[disabled] + .checkbox-custom + .checkbox-label {
  color: rgba(255, 255, 255, 0.06) !important;
  cursor: default; }

.checkbox-old:checked[disabled] + .checkbox-custom {
  background: rgba(255, 255, 255, 0.2) !important;
  border: 1px solid rgba(255, 255, 255, 0) !important;
  transition: 0.3s; }

.checkbox-old:checked[disabled] + .checkbox-custom::before {
  content: "";
  display: block !important;
  position: absolute;
  left: 4px;
  top: 5px;
  width: 10px;
  height: 5px;
  border-bottom: 2px solid rgba(0, 0, 0, 0.2) !important;
  border-left: 2px solid rgba(0, 0, 0, 0.2) !important;
  transform: rotate(-50deg) skew(-5deg);
  border-radius: 2px;
  transition: 0.3s; }

.checkbox-old:checked[disabled] + .checkbox-custom + .checkbox-label {
  color: rgba(255, 255, 255, 0.16) !important; }

.checkbox:hover .checkbox-custom {
  background: rgba(255, 255, 255, 0.1);
  border: 1px solid #FFFFFF; }

.checkbox:hover .checkbox-label {
  color: rgba(255, 255, 255, 0.8); }

.checkbox:hover .checkbox-old:checked + .checkbox-custom {
  background: #FFFFFF; }

.checkbox:hover .checkbox-old:checked + .checkbox-custom::before {
  border-color: rgba(0, 0, 0, 0.3); }

.checkbox.wrong .checkbox-custom {
  border-color: red; }

@media (max-width: 560px) {
  .checkbox-custom {
    height: 16px;
    width: 16px; }
  .checkbox-label {
    font-size: 10px;
    line-height: 15px;
    padding-left: 13px; } }

